html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
    margin:0;
    padding:0;
    border:0;
    outline:0;
    font-size:100%;
    vertical-align:baseline;
    background:transparent;
}

body {
    line-height:1;
}

article,aside,details,figcaption,figure,
footer,header,hgroup,menu,nav,section { 
    display:block;
}

nav ul {
    list-style:none;
}

blockquote, q {
    quotes:none;
}

blockquote:before, blockquote:after,
q:before, q:after {
    content:'';
    content:none;
}

a {
    margin:0;
    padding:0;
    font-size:100%;
    vertical-align:baseline;
    background:transparent;
}

/* change colours to suit your needs */
ins {
    background-color:#ff9;
    color:#000;
    text-decoration:none;
}

/* change colours to suit your needs */
mark {
    background-color:#ff9;
    color:#000; 
    font-style:italic;
    font-weight:bold;
}

del {
    text-decoration: line-through;
}

abbr[title], dfn[title] {
    border-bottom:1px dotted;
    cursor:help;
}

table {
    border-collapse:collapse;
    border-spacing:0;
}

/* change border colour to suit your needs */
hr {
    display:block;
    height:1px;
    border:0;   
    border-top:1px solid #cccccc;
    margin:1em 0;
    padding:0;
}

input, select {
    vertical-align:middle;
}
textarea{
   resize: none;
}

body{
   background-color: #C8CEFE;
}

.content-block{
   margin: 30px auto 0;
   max-width: 400px;
   background-color: #fff;
   border-radius: 56px;
   padding: 40px;
   display: flex;
   justify-content: center;
}

.field, .fieldTextarea{
   margin: 15px 0;
}

.error-message{
   display: block;
   margin: 5px auto;
   color: crimson;
}

.symblos-left{
   display: block;
   margin: 3px auto;
   color: rgb(42, 34, 36);
   font-size: 10px;
}

.symblos-limit{
   display: block;
   margin: 3px auto;
   color: rgb(211, 63, 100);
   font-size: 11px;
}

.content-block__result-form{
   max-width: 400px;
   background-color: #fff;
   border-radius: 56px;
   display: none;
   justify-content: center;
}

.result-entry{
   margin: 10px auto;
   border: 2px dotted rgb(51, 42, 42);
   padding: 10px;
}



